export class Icon {

    #elem
    #label


    get element() {
        return this.#elem;
    }

    get label() {
        return this.#label;
    }


    constructor(label) {

        if (!isString(label)) {
            throw new TypeError('name must be a String');
        }

        if (!icons[label]) {
            throw new Error('Invalid label ' + label);
        }


        this.#label = label;
        this.#elem = createElement('i', { class: icons[label] + ' font-face' });

    }

}


export default Icon;